import React, { Component } from "react";
import { Circle, Line } from "rc-progress";

class SkillProgress extends Component {
  constructor(props) {
    super(props);

    this.state = {
      isPageFocus: false, //í˜ì´ì§€ê°€ í¬ì»¤ìŠ¤ ë˜ì—ˆì„ ë•Œ true
      percent: 0, //progress ê°’
    };
  }

  componentDidUpdate(prevProps, prevState) {
    const { isPageFocus } = this.props;

    //Career í˜ì´ì§€ê°€ ë„ì›Œì§€ê³  stateì˜ isPageFocus ê°’ì´ false ì¼ë•Œ
    //Career í˜ì´ì§€ê°€ ë„ì›Œì ¸ì„ë•Œ
    if (!prevState.isPageFocus && isPageFocus) {
      this.increase();
      this.setState({
        isPageFocus: isPageFocus,
      });
    }

    //Careerì—ì„œ ë‹¤ë¥¸ í˜ì´ì§€ë¡œ ì´ë™í–ˆì„ë•Œ ë‚˜ë¨¸ì§€ ê°’ë“¤ ì´ˆê¸°í™”
    if (prevState.isPageFocus && !isPageFocus) {
      this.setState({
        isPageFocus: false,
        percent: 0,
      });
    }
  }

  //progressbar ê°’ ì¦ê°€ì‹œí‚¤ëŠ” ë©”ì†Œë“œ
  increase = () => {
    const { percent } = this.state;
    const { percentVal } = this.props;
    const newPercent = percent + 1; //percent ê°’ì„ 1ì”© ì¦ê°€ì‹œì¼œ ì €ì¥í•´ë†“ëŠ” ë³€ìˆ˜

    if (newPercent >= percentVal + 1) {
      clearTimeout(this.tm); //timerë¥¼ clearí•¨
      return;
    }
    this.setState({ percent: newPercent });
    this.tm = setTimeout(this.increase, 50); //0.05ì´ˆ ë§ˆë‹¤ í˜¸ì¶œ
  };

  render() {
    const { percent } = this.state;
    const { color, contents } = this.props;

    const containerStyle = {
      width: "400px",
    };

    return (
      <div className="skill-progress-container" style={containerStyle}>
        <Line percent={percent} strokeWidth="1" strokeColor={color} />
        <div className="graph-contents">{contents}</div>
      </div>
    );
  }
}

export default SkillProgress;
