import React, { Component } from "react";
import Header from "components/Header/Header";
import FullPage from "components/FullPage/FullPage";
import Launcher from "components/Launcher/Launcher";

import { connect } from "react-redux";
import { setAppState } from "store/modules/AppConfig";

const mapStateToProps = ({}) => ({});

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      isSetTimeOut: false, //ëŸ°ì³ í™”ë©´ ë³´ì—¬ì£¼ê¸° ìœ„í•œ state ê°’
      showPageName: null, //í˜„ì¬ ë³´ì—¬ì§€ëŠ” í˜ì´ì§€ ì €ì¥
    };
  }

  componentDidMount() {
    const { setAppState } = this.props;
    setAppState(this.state); //Appì˜ state ê°’ì„ ì €ì¥
  }

  componentDidUpdate(prevProps, prevState) {
    const { setAppState } = this.props;

    if (this.state !== prevState) {
      setAppState(this.state);
    }
  }

  //í˜„ì¬ ë³´ì—¬ì§€ëŠ” í˜ì´ì§€ ì´ë¦„ ê°€ì§€ê³  ì˜¤ëŠ” method
  handlePageChange = (pageName) => {
    this.setState({
      showPageName: pageName,
    });
  };

  render() {
    const { isSetTimeOut, showPageName } = this.state;

    if (!isSetTimeOut) {
      setTimeout(() => {
        this.setState({ isSetTimeOut: true });
      }, 2000);
    }

    return (
      <div>
        {!isSetTimeOut && (
          <div style={{ width: "100%", height: "100%" }}>
            <Launcher />
          </div>
        )}
        {isSetTimeOut && (
          <div className="app-container">
            <FullPage
              className="full-page"
              handlePageChange={this.handlePageChange}
            />
            <Header showPageName={showPageName} />
          </div>
        )}
      </div>
    );
  }
}

export default connect(mapStateToProps, { setAppState })(App);
